<script>
import tableComponent from "@/components/Table/Table.vue";
import UploadComponent from "@/components/Upload/Upload.vue";
import config from "../../../public/config/customSetting.json";

export default {
  name: "applyCourse-component",
  components: { tableComponent, UploadComponent },
  data() {
    return {
      dialogVisible: false,
      config: config,
      fileIndex: 0,
      selectedItem: [],
      psd: "",
      psdInput: "",
      scheduleNo: "",
      form: {
        available_Begin_Date: "",
        schedule_No: "",
        applyPeople: [],
        class_Date: "",
      },
      rules: {
        schedule_No: [{ required: true, message: "必填", trigger: "change" }],
        applyPeople: [{ required: true, message: "必填", trigger: "change" }],
      },
      cancelTableColumns: [
        {
          id: "index",
          label: "項次",
          width: "70",
        },
        {
          id: "col1",
          label: "姓名 (性別) 身分證字號",
          width: "180",
        },
        {
          id: "signUpStatus",
          label: "報名狀態",
          width: "125",
        },
        //2024.04.17 新增退件說明
        {
          id: "reject_reason",
          label: "退件原因",
          width: "100",
        },
        {
          id: "fileName",
          label: "入廠危害告知附檔",
          width: "150",
        },
        {
          id: "col3",
          label: "保險展延期間 (起~迄)",
          width: "200",
        },
        {
          id: "col4",
          label: "上課日期/課程狀態",
          width: "150",
        },
        {
          id: "col5",
          label: "報名日期/課程名稱",
          width: "225",
        },
        {
          id: "room_Desc",
          label: "課程地點",
          width: "150",
        },
        {
          id: "p_She_Class_Date",
          label: "最近上課日期",
          width: "150",
        },
      ],
      courseTableColumns: [
        {
          id: "index",
          label: "項次",
          width: "70",
        },
        {
          id: "col2",
          label: "上課日期",
          width: "200",
        },
        {
          id: "room_Description",
          label: "教室地點",
          width: "200",
        },
        {
          id: "col4",
          label: "上課時間",
          width: "150",
        },
        {
          id: "col5",
          label: "課程名稱(課表編號)",
          width: "350",
        },
        {
          id: "col6",
          label: "已報名人數/人數上限",
          width: "175",
        },
        {
          id: "status_Id",
          label: "開課狀態",
          width: "100",
        },
      ],
      course1Data: [],
      course2Data: [],
      coursePeople: [],
      course2List: [],
      cancelSignUpData: [],
      cancelSignUpList: [],
      cancelSignUpPage: 1,
      courseListPage: 1,
    };
  },
  computed: {},
  methods: {
    // API取得資料 & 資料整理
    async getData() {
      const courseListRes = await this.$store.dispatch("qryCourseList");
      const courseListData = courseListRes.data.data;
      this.course1Data = JSON.parse(JSON.stringify(courseListData.course1));
      this.course2Data = JSON.parse(JSON.stringify(courseListData.course2));
      this.course1Data = this.course1ListColumns(this.course1Data);
      this.course2Data = this.course2ListColumns(this.course2Data);
      this.course2List = this.course2Data.slice(0, 10);
      const cancelSignUpRes = await this.$store.dispatch("cancelSignUpList");
      const signUpData = cancelSignUpRes.data.data;
      this.cancelSignUpData = JSON.parse(JSON.stringify(signUpData));
      this.cancelSignUpData = this.cancelSignUpListColumns(
        this.cancelSignUpData
      );
      this.cancelSignUpList = this.cancelSignUpData.slice(0, 10);
    },
    // 分頁操作
    cancelSignUpChange(page) {
      this.cancelSignUpPage = page;
      const cur = (page - 1) * 10;
      const last = page * 10;
      this.cancelSignUpList = this.cancelSignUpData.slice(cur, last);
    },
    // 分頁操作
    courseListChange(page) {
      this.courseListPage = page;
      const cur = (page - 1) * 10;
      const last = page * 10;
      this.course2List = this.course2Data.slice(cur, last);
    },
    // 暫時沒有用到
    beginDateHandler(evt, index) {
      console.log("people." + index);
      if (evt) {
        this.$refs.form.clearValidate("people." + index);
      } else {
        this.$refs.form.validateField("people." + index);
      }
    },
    // 檔案上傳事件
    fileChangeHandler(file) {
      this.form.people.forEach(async (e, index) => {
        if (index === this.fileIndex) {
          e.fileName = file.name;
          e.fileBase64 = await this.toBase64(file.raw);
          e.uid = file.uid;
        }
      });
      this.$refs.form.clearValidate("people." + this.fileIndex + ".fileName");
    },
    fileRemoveHandler(file) {
      this.form.people.forEach((e) => {
        if (e.uid === file.uid) {
          e.fileName = "";
          e.fileBase64 = "";
          e.uid = "";
        }
      });
      this.$refs.form.validateField("people." + this.fileIndex + ".fileName");
    },
    courseChangeHandler(item) {
      const course_Id = item.course_Id;
      this.scheduleNo = item.schedule_No;
      this.form.class_Date = item.class_Date;
      this.form.available_Begin_Date = item.available_Begin_Date;
      this.$store.dispatch("qryCoursePeople", course_Id).then((res) => {
        const data = res.data.data;
        this.coursePeople = data;
        this.coursePeople.forEach((e) => (e.label = `${e.p_Name}(${e.id})`));
      });
      this.form.people = [];
      this.form.applyPeople = [];
      if (item.special_Class === "Y") {
        this.dialogVisible = true;
        this.psd = item.password;
      }
    },
    closeDialog() {
      this.form.schedule_No = "";
      this.dialogVisible = false;
      this.psdInput = "";
    },
    // 檢查密碼一致
    submitPsdDialog() {
      if (this.psd === this.psdInput) {
        this.dialogVisible = false;
        this.$message({
          type: "success",
          duration: "5000",
          showClose: true,
          message: "密碼正確",
        });
      } else {
        this.$message({
          type: "error",
          duration: "5000",
          showClose: true,
          message: "密碼輸入錯誤",
        });
        this.$refs.psdInput.focus();
      }
      this.psdInput = "";
    },
    peopleChangeHandler(item) {
      this.form.people = item;
    },
    selectionChangeHandler(val) {
      this.selectedItem = val;
    },
    // 全選框隱藏判斷
    canSelectRow(row) {
      if (
        row.available_Begin_Date <= this.formatDate(new Date()) ||
        row.approve_Status === "2"
      ) {
        return false;
      } else {
        return true;
      }
    },
    // API 更新資料
    updCancelSignUp() {
      this.$store.dispatch("updCancelSignUp", this.selectedItem).then((res) => {
        if (res.data.isOK) {
          this.$message({
            type: "success",
            duration: 5000,
            showClose: true,
            message: "修改成功",
          });
          this.cancelSignUpPage = 1;
          this.getData();
          this.$refs.cancelMultipleTable.$refs.multipleTable.clearSelection();
        }
      });
    },
    submitForm(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          this.form.schedule_No = this.scheduleNo;
          // 目前所選的課程
          let targetCourse = null;
          // 課程可報名數量
          let targetCanApplyCourseCount = null;
          targetCourse = this.course1ListColumns(this.course1Data).find(
            (e) => this.form.schedule_No === e.schedule_No
          );
          targetCanApplyCourseCount =
            parseInt(targetCourse.min_Audience) - targetCourse.register_Count;
          if (targetCanApplyCourseCount - 1 >= 0) {
            this.$store.dispatch("saveCoursePeople", this.form).then((res) => {
              if (res.data.isOK) {
                this.$message({
                  type: "success",
                  duration: "5000",
                  showClose: true,
                  message: "送出成功",
                });
                this.resetForm(formName);
                this.getData();
              }
            });
          } else {
            this.$message({
              type: "error",
              duration: "5000",
              showClose: true,
              message: "人數已滿，無法報名",
            });
          }
        } else {
          this.$message({
            type: "error",
            duration: "5000",
            showClose: true,
            message: "資料填寫錯誤",
          });
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
      this.form.people = [];
    },
    // 資料整理
    course1ListColumns(formData) {
      formData.forEach((e, index) => {
        e.index = index + 1;
        e.label = `${e.class_Date}(${e.whatday}), ${e.course_Name}-${
          e.schedule_No
        }, ${e.room_Description}(${e.begin_Time}), 剩${
          e.min_Audience - e.register_Count
        }${e.special_Class === "Y" ? `(資格限定)` : ""}`;
      });
      return formData;
    },
    // 資料整理
    course2ListColumns(formData) {
      formData.forEach((e, index) => {
        e.index = index + 1;
        e.col2 = `${e.class_Date}(${e.whatday})`;
        e.col4 = `${e.begin_Time} ~ ${e.end_Time}`;
        e.col5 = `${e.course_Name}(${e.schedule_No})`;
        e.col6 = `${e.register_Count}/ ${e.min_Audience}`;
      });
      return formData;
    },
    // 資料整理
    cancelSignUpListColumns(formData) {
      formData.forEach((e, index) => {
        e.index = index + 1;
        e.col1 = `${e.p_Name}/${e.sex}/${e.id}`;
        e.signUpStatus = e.available_Begin_Date;
        e.approveStatus = e.approve_Status;
        e.col3 = `${e.tmpinsurancebegin} ~ ${e.tmpinsuranceend}`;
        e.col4 = `${e.available_Begin_Date}/${e.c_s_Status_Description}`;
        e.col5 = `${e.available_Begin_Date} / ${e.course_Name}`;
        e.fileName = e.file_Name;
      });
      return formData;
    },
  },
  mounted() {
    this.getData();
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;
  },
};
</script>

<template src="./applyCourse.html"></template>
<style lang="sass" scoped src="./applyCourse.sass"></style>
